import React from 'react';
import StaticContainer from '../../containers/static';

const Policy = () => (
    <StaticContainer>
        <>
            <h1>Политика конфиденциальности</h1>
            <p>Администрация ресурса Squadix уважает ваше право на конфиденциальность.</p>

            <p>Настоящая политика информирует пользователей ресурса Squadix о правилах конфиденциальности, применяемых на ресурсе Squadix в отношении ваших личных данных, основана на нормах законодательства Республики Беларусь, международного законодательства в области защиты личных данных. Эти принципы и нормы применяются к обработке ваших личных данных при пользовании ресурсом Squadix и его посещении для защиты вашей частной жизни.</p>

            <p>Пользуясь ресурсом Squadix, вы доверяете нам ваши личные данные. Доверие наших пользователей имеет для нас первостепенное значение, и поэтому Squadix стремится защищать ваше право на конфиденциальность. Политика конфиденциальности объясняет, как мы собираем и используем ваши личные данные и каким образом мы защищаем ваше право на конфиденциальность.</p>

            <p>Если вы не принимаете условия нашей политики конфиденциальности, мы просим вас воздержаться от размещения объявлений, посещения, а также от пользования прочими сервисами ресурса Squadix.</p>

            <h3>1. Общие положения об обработке личных данных пользователей</h3>
            <p>Мы обрабатываем личные данные пользователей с целью реализации нашего <a href="/terms">Пользовательского соглашения</a> и <a href="/rules">Правил размещения объявлений</a>, а также для улучшения и совершенствования наших сервисов. Мы также занимаемся обработкой личных данных для того, чтобы предоставить вам информацию маркетингового характера, а также прочий персонализированный контент с учетом ваших предпочтений; для понимания тенденций в нашем сегменте рынка и для пресечения попыток злоупотребления нашими сервисами. Ваши личные данные могут быть переданы нашим партнерам для использования ими в целях, установленных настоящей Политикой конфиденциальности.</p>

            <p>Личные данные, собираемые нами в указанных выше целях, включают в себя:</p>

            <ul>
                <li>данные, которые пользователи самостоятельно указывают на ресурсе Squadix (имя, электронный адрес, номер телефона и т.д.)</li>
                <li>данные, которые автоматически собираются при пользовании нашими сервисами, например, при посещении страниц ресурса Squadix, в том числе данные о географическом местоположении пользователя, об устройствах, используемых пользователями для посещения ресурса Squadix, а также о типе используемого интернет-подключения.</li>
            </ul>
            <h3>2. Основные понятия и задачи политики конфиденциальности</h3>
            <p><strong>2.1.</strong> Под политикой конфиденциальности понимается заявление публичного характера о том, какие данные о пользователях собираются на ресурсе Squadix, как эти данные используются, хранятся и раскрываются третьим лицам, а также о том, как частные лица, чьи данные задействованы на ресурсе Squadix, могут управлять или распоряжаться такой информацией (например, изменять свои личные данные; отказывается или соглашаться на получение рассылок от ресурса Squadix).</p>

            <p><strong>2.2.</strong> Политика конфиденциальности призвана обеспечить и гарантировать информирование всех пользователей и посетителей ресурса Squadix о том, какие данные о пользователях и посетителях собираются ресурсом Squadix, каким образом это происходит и в каких целях.</p>
            <p><strong>2.3.</strong> Политика конфиденциальности является неотъемлемой частью <a href="/terms">Пользовательского соглашения</a> и <a href="/rules">Правил размещения объявлений</a> на ресурсе Squadix, в которых дается отсылка к непосредственному тексту Политики конфиденциальности, применяемой в отношении пользователей и посетителей ресурса Squadix.</p>
            <p><strong>2.4.</strong> Под личными данными понимаются:</p>
            <p><strong>2.4.1.</strong> любые данные, позволяющие напрямую идентифицировать личность пользователя (например, имя пользователя, электронный адрес, номер телефона и т.п.);</p>
            <p><strong>2.4.2.</strong> любые данные, на основании которых возможно идентифицировать личность пользователя, отдельно или в комбинации с другой доступной информацией (например, дата рождения и адрес);</p>
            <p><strong>2.4.3.</strong> любые другие дополнительные данные, которые связаны с данными из <em>пп. 2.4.1 и 2.4.2</em> (например, поведение и события, заказы, информационные сессии и т.п.).</p>
            <p><strong>2.5.</strong> Обработка личных данных - любое использование личных данных, например, их сбор, регистрация, сопоставление, хранение и передача, или комбинация любых из этих действий. Вся обработка личных данных осуществляется на основании Конституции Республики Беларусь, Закона Республики Беларусь от 10 ноября 2008 г. № 455-3 “Об информации, информатизации и защите информации”, а также иных актов законодательства Республики Беларусь, а также международных актов, применяемых в отношении обработки и защиты личных данных.</p>
            <p><strong>2.6.</strong> Ответственным за сбор, хранение, обработку и распространение личных данных пользователей ресурса Squadix является администрация ресурса. По всем вопросам вы можете обратиться на электронный адрес: <a href="mailto:strike.app.service@gmail.com">strike.app.service@gmail.com</a>.</p>
            <h3>3. Принципы сбора и обработки личных данных пользователей</h3><p>Мы стремимся защитить вашу частную жизнь и право на конфиденциальность при обработке личных данных, вне зависимости от способов или методов сбора таких данных, с учетом следующих стандартов их обработки:</p>
            <p><strong>3.1.</strong> Честная и законная обработка ваших данных. Ваши личные данные будут обрабатываться в случае, если вы дали на это согласие или обработка ваших данных допускается законодательством Республики Беларусь.</p>
            <p><strong>3.2.</strong> Явное указание цели обработки ваших личных данных. Личные данные должны собираться и обрабатываться только в четко обозначенных и законных целях и не могут быть обработаны с нарушением этого принципа.</p>
            <p><strong>3.3.</strong> Соответствие собираемых и обрабатываемых данных указанным целям. Сбор, обработка и использование ваших личных данных должны быть адекватными и соответствующими целям. Мы стремимся собрать минимальный объем информации, который необходим для осуществления поставленной задачи.</p>
            <p><strong>3.4.</strong> Ограниченность сроков хранения личных данных. Хранение личных данных пользователей осуществляется в течение определенных сроков. Если использование хранящихся данных перестает быть необходимым, мы предпримем меры по уничтожению или прекращению обработки ваших личных данных в разумные сроки, принимая во внимание функциональные и законодательные требования, касающиеся хранения и конфиденциальности личных данных.</p>
            <p><strong>3.5.</strong> Качество и целостность. Личные данные должны быть точными, достоверными, полными и актуальными для достижения целей их обработки. В случае необходимости мы будем предпринимать меры по изменению или удалению некорректных или неполных данных.</p>
            <p><strong>3.6.</strong> Прозрачность и информирование о целях сбора личных данных. Мы открыто сообщаем о факте сбора ваших личных данных, целях сбора и обработки личных данных, круге лиц, чья информация собирается, использовании файлов cookie, наименовании и местонахождении организации, ответственной за обработку данных и т.п.</p>
            <p><strong>3.7.</strong> Согласие на обработку личных данных и право выбора. В случае обработки ваших данных мы просим ваше согласие, если иное не предусмотрено законодательством Республики Беларусь. В случае, если вы не согласны со сбором и обработкой ваших личных данных, то просим вас воздержаться от пользования ресурсом Squadix.</p>
            <p><strong>3.8.</strong> Безопасность личных данных. Личные данные должны быть защищены надлежащим образом от возможности неправомерного доступа, потери, использования, изменения и раскрытия.</p>
            <h3>4. Какие личные данные о наших пользователях мы собираем и обрабатываем?</h3><p>Мы собираем информацию о пользователях, предоставляемую ими при регистрации профиля, подаче объявления, а также при пользовании нашими сервисами (например, информация о посещенных страницах; о способе использования сервисов; техническая информация). Мы собираем следующие виды данных о вас:</p>
            <h4>4.1. Данные, самостоятельно вносимые пользователями</h4><p>При регистрации профиля, размещении объявления, а также в иных случаях вы оставляете данные, которые впоследствии сохраняются. Эти данные хранятся в нашей базе данных, чтобы мы смогли предоставить вам запрашиваемую вами услугу, совершенствовать наши сервисы, пресечь неправомерное использование наших сервисов, а также предотвратить и отследить возможные правонарушения и преступления. Мы также храним информацию, предоставляемую вами при обращении в нашу службу поддержки или иные службы ресурса, чтобы иметь возможность связаться с вами и предоставить вам ответ на ваше обращение. Мы храним все замечания и предложения, направляемые вами в наш адрес, так как они помогают нам в совершенствовании наших сервисов.</p>
            <h4>4.2. Данные, автоматически собираемые при использовании наших сервисов</h4><p>При использовании наших сервисов мы регистрируем данные о видах используемых сервисов, о способе их использования, а также о посещаемых страницах. Эти данные можно разделить на следующие виды:</p>
            <h4>4.2.1. Данные технического характера об устройстве и интернет-подключении</h4><p>С помощью логов сервера и других инструментов мы регистрируем данные о техническом устройстве, которым вы пользуетесь, и каким образом это устройство подключается к нашим сервисам, включая данные об операционной системе, версии браузера, IP-адресах, файлах cookie и уникальных идентификационных файлах. Обычно эти данные используются для получения анонимной (обезличенной) и совокупной статистики, но она также может быть связана с вашей учетной записью (профилем). Эта информация дает нам возможность анализировать, как вы пользуетесь нашими сервисами, и подстраивать их под ваше устройство в целях облегчения и удобства вашего пользования ресурсом Squadix.</p>
            <h4>4.2.2. Данные о пользовании нашими сервисами</h4><p>Когда вы входите в или выходите из вашего профиля, ищете товары или услуги на Squadix, размещаете объявление, связываетесь с продавцом или пользуетесь нашим ресурсом иным образом, информация об этом автоматически регистрируется в логах сервера. Мы используем эту информацию для того, чтобы препятствовать злоупотреблениям в отношении наших сервисов, правонарушениям и преступлениям, совершенствовать наши сервисы, обеспечивать отображение персонализированного и релевантного контента (например, показ объявлений в вашем регионе и отображение объявлений, соответствующих вашим недавним запросам).</p>
            <h4>4.2.3. Данные о местоположении</h4><p>Когда вы пользуетесь нашими сервисами, мы фиксируем ваше примерное географическое местоположение, основанное на вашем IP-адресе. Если вы пользуетесь нашими мобильными приложениями, мы можем собирать более точные данные о вашем местоположении. Эта информация используется, например, для статистических исследований, а также для того, чтобы отображать актуальные для вас объявления, рекламу или другой персонализированный контент.</p>
            <h4>4.2.4. Файлы cookie и прочий локально хранящийся контент</h4><p>Когда вы пользуетесь Squadix, мы используем различные инструменты, чтобы распознать вас как нашего пользователя и упростить ваше пользование ресурсом Squadix. Файл cookie - это небольшой файл данных (фрагмент текста), который передается в ваш браузер от Squadix. Такие файлы содержат информацию, которую мы впоследствии можем прочитать и упростить для вас использование нашего ресурса и сервисов. Файлы cookie никогда не используются для запуска программ или для заражения вашего устройства вирусами. Мы используем файлы cookie, например, для того, чтобы вам не нужно было каждый раз заново вводить данные для входа в профиль. Squadix также отправляет вам файлы cookie, например, когда вы перемещаетесь по нашему ресурсу, осуществляете поиск или вносите ваши данные при регистрации и т.д. Файлы cookie позволяют сэкономить ваше время, поскольку они позволяют сохранять, а не вводить многократно одну и ту же информацию. Мы можем локально хранить анонимные идентификационные файлы на вашем устройстве. Это касается в первую очередь мобильных устройств, которые не поддерживают использование файлов cookie. Мы используем следующие виды файлов cookie: аналитические, социальных сетей, партнеров, cookies рекламы и поведения, технические и рабочие. Пользуясь нашими сервисами, вы выражаете ваше согласие на использование нами файлов cookie, если в настройках вашего браузера вы не деактивировали использование файлов cookie. Принимая файлы cookie на Squadix, вы предоставляете нам доступ к информации о том, какие страницы вы просматриваете. Эта информация может быть использована для настройки персонализированного контента на Squadix, а также для статистических исследований. Информацию об изменении настроек файлов cookie можно получить в разделе справки вашего браузера. Вы можете сами решить, согласны ли вы принимать файлы cookie или нет. В случае если в вашем браузере настроено сохранение файлов cookie, то вы тем самым соглашаетесь с сохранением файлов cookie, поступающих от Squadix. В настройках вашего браузера вы можете выбрать автоматическое сохранение файлов cookie; установить опцию, чтобы ваше согласие требовалось при каждом сохранении файлов cookie; установить опцию, чтобы файлы cookie вовсе не сохранялись в вашем браузере. Как изменить эти настройки в вашем браузере - зависит от браузера, которым вы пользуетесь. Вам необходимо внести изменения в настройки всех браузеров, которые установлены на вашем устройстве, чтобы настройки в отношении файлов cookie работали корректно и единообразно. При отказе от приема файлов cookie на Squadix некоторые из наших сервисов могут отображаться некорректно. Здесь вы можете ознакомиться с информацией о том, как изменить настройки для файлов cookie в некоторых браузерах:</p>
            <ul><li><span><a href="https://support.apple.com/ru-ru/guide/safari/sfri11471/11.0/mac/10.13" target="_blank" rel="noreferrer">Safari</a></span></li><li><a href="https://support.google.com/chrome/answer/95647?co=GENIE.Platform%3DDesktop&amp;hl=ru" target="_blank" rel="noreferrer">Google Chrome</a></li><li><a href="https://support.microsoft.com/ru-ru/help/17442/windows-internet-explorer-delete-manage-cookies" target="_blank" rel="noreferrer">Explorer</a></li><li><a href="https://support.mozilla.org/ru/kb/vklyuchenie-i-otklyuchenie-kukov-ispolzuemyh-veb-s" target="_blank" rel="noreferrer">Mozilla Firefox</a></li><li><a href=" https://help.opera.com/ru/latest/security-and-privacy/" target="_blank" rel="noreferrer">Opera</a></li></ul><h4>4.3. Данные из других источников</h4><p>Иногда мы можем получать данные из других источников, например, от рекламных партнеров, ресурсов и инструментов по сбору статистических данных и других третьих лиц, которые помогают нам понять вашу пользовательскую активность, ваши предпочтения и совершенствовать наши сервисы. Мы можем получать данные от наших партнеров, например, о том, что будет происходить после того, как вы нажмете на рекламный баннер на Squadix. Такая информация поможет нам оценить эффективность баннерной рекламы и сделать ее более актуальной для пользователя.</p>
            <h3>5. Для каких целей используются личные данные пользователей?</h3><p><strong>Squadix </strong>обрабатывает ваши данные для того, чтобы оказывать вам услуги, улучшать и развивать наши сервисы (например, настроить их согласно вашим предпочтениям). Мы также обрабатываем личные данные, чтобы предоставить вам персонализированные рекомендации и предложения, персонализированный контент, пресечь попытки нарушений, а также в коммерческих (рекламных, маркетинговых) целях. Мы используем личные данные для аналитических целей для того, чтобы понять тенденции рынка и попытаться предсказать будущие тенденции. Ниже вы сможете более детально ознакомиться с целями, для которых мы используем ваши личные данные.</p>
            <h4>5.1. Персонализированные сервисы и маркетинг</h4><p>Мы хотим рекомендовать вам контент и сервисы на основании вашего пользовательского поведения и ваших интересов. В первую очередь, мы можем дать вам рекомендации об объявлениях и сервисах, основанные на вашем поведении на ресурсе Squadix. Мы также можем порекомендовать продукты и сервисы, основываясь на предпочтениях других пользователей с похожим пользовательским поведением. Мы также можем использовать ваши личные данные, чтобы настроить баннерную рекламу и другой коммерческий контент на ресурсе Squadix согласно вашим предпочтениям. На основе такой информации мы также можем осуществлять рекламные кампании. Рекламодатели, размещающие у нас баннерную рекламу, могут получить доступ к вашим личным данным и вашим пользовательским предпочтениям для отображения актуальной для вас баннерной рекламы, однако им строго запрещено использовать эти данные в других целях, отличных от размещения баннерной рекламы на Squadix.</p>
            <h4>5.2. Прямой маркетинг</h4><p>Периодически мы направляем нашим пользователям письма маркетингового характера (рассылку) по электронной почте или по другим каналам (например, в социальных сетях), если вы не отказались от получения такой рассылки. Эта рассылка может содержать информацию о специальных предложениях от третьих лиц. Если вы не являетесь нашим пользователям, то такая рассылка не будет вам направляться без вашего явного предварительного согласия. В письмах рекламного характера, которые вы получаете от нас, есть возможность отказаться от рассылки, нажав на специальную ссылку в письме.</p>
            <h4>5.3. Совершенствование наших сервисов</h4><p>Мы используем ваши личные данные, чтобы предоставить вам персонализированные сервисы, соответствующие вашим потребностям и интересам, а также чтобы усовершенствовать наш интерфейс. Информация, собранная в этих целях, агрегируется и анонимизируется . Однако эту информацию мы также можем использовать не в анонимной форме при необходимости разрешения технических проблем, возникших у отдельных пользователей.</p>
            <h4>5.4. Пресечение попыток злоупотребления нашими сервисами</h4><p>Мы используем техническую информацию и информацию об активности пользователей, чтобы ограничить разные виды злоупотреблений нашими сервисами и преступную активность на Squadix. Под злоупотреблением нашими сервисами понимается мошенничество, спам-рассылка, фишинг, предложения интимного характера, попытки входа в чужие учетные записи (профили), а также другие действия, которые запрещены нашими <a href="/rules">Правилами размещения объявлений</a>, <a href="/policy">Пользовательским соглашением</a> и законодательством Республики Беларусь.</p>
            <h4>5.5. Разработка продукта и анализ тенденций в нашем сегменте</h4><p>Целью обработки личных данных пользователей также является получение статистики о тенденциях в нашем сегменте, которую мы используем для того, чтобы совершенствовать Squadix, а также расширять круг наших продуктов и сервисов. Статистика может собираться с помощью анализа, который может проводиться как самим ресурсом Squadix, так и компаниями, к услугам которых мы прибегаем для этих целей. Такие компании анализируют информацию на основе конфиденциальности и удаляют ее после выполнения поставленной задачи.</p>
            <h4>5.6. Предоставление дополнительных сервисов</h4><p>В случае привлечения нами третьих лиц для предоставления пользователям ресурса Squadix отдельных сервисов (услуг), личные данные пользователей, необходимые для предоставления им таких сервисов (услуг), могут собираться с целью их передачи третьим лицам, непосредственно осуществляющим предоставление соответствующих сервисов (услуг).</p>
            <h4>5.7. Централизованное хранение</h4><p>Собираемые нами личные данные могут быть сохранены и обработаны в централизованной базе данных с целью понять вашу пользовательскую активность.</p>
            <h3>6. Предоставление личных данных третьим лицам</h3><p>Ваши личные данные об использовании наших сервисов могут быть переданы другим компаниям, аффилированным Squadix, для использования в тех целях, для которых эта информация была собрана, и только в целях, указанных в п. 5 настоящей Политики конфиденциальности. По возможности информация будет предоставлена в анонимной (обезличенной) форме, однако в некоторых случаях такая информация может допускать вашу идентификацию. Для того, чтобы сделать ваше объявление как можно более эффективным, мы можем передать информацию, которую вы указали в вашем объявлении, нашим партнерам или подрядчикам.</p>
            <p>Также ваши данные могут быть переданы третьим лицам, например, при проведении вами платежа или при хранении информации на сервере третьих лиц, а также для организации доставки реализуемых/приобретаемых посредством Squadix товаров. Однако наши соглашения с партнерами и подрядчиками защищают ваше право на конфиденциальность и запрещают использование ими ваших данных в иных целях. В случае если есть основания полагать, что посредством Squadix было совершено правонарушение или преступление, по официальному запросу компетентных в расследовании органов и в соответствии с законодательством Республики Беларусь, ваши личные данные, данные переписки и информация по вашим объявлениям может быть передана таким органам. Кроме того, такие данные могут быть переданы соответствующим органам и по нашей инициативе в целях предотвращения планируемых правонарушений и преступлений, если у нас есть весомые основания считать, что противоправные действия могут быть совершены в отношении третьих лиц или наших сервисов.</p>
            <p>Мы не будем использовать, продавать, передавать или раскрывать ваши личные данные иным способом и в иных целях, которые не указаны в настоящей Политике конфиденциальности, если мы не обязаны раскрывать ваши личные данные по решению суда или в случае если мы предварительно не получили ваше явное согласие на это. Мы можем передавать сведения личного характера о вас в случае предварительного получения вашего согласия на это. Если ваши личные данные переданы третьим лицам в связи с оказанием нам услуг в соответствии с соглашениями, то такие третьи лица будут обязаны конфиденциально обрабатывать ваши данные и не сможет использовать эту информацию в каких-либо иных целях.</p>
            <p>Также ваши данные могут быть переданы третьим лицам, например, при проведении вами платежа или при хранении информации на сервере третьих лиц, а также для организации доставки реализуемых/приобретаемых посредством Squadix товаров или при проведении мероприятий по предотвращению мошенничества.</p>
            <h3>7. Запрос о предоставлении, редактировании или удалении своих личных данных</h3><p>Для предоставления, редактирования или удаления ваших данных вы можете обратиться по электронному адресу: <a href="mailto:strike.app.service@gmail.com">strike.app.service@gmail.com</a>. В случае обращения в письменном виде по данному адресу один раз в год мы можем бесплатно предоставить вам информацию о ваших данных, которыми Squadix обладает. Также в письменной форме в любое время вы можете попросить нас внести изменения в ваши личные данные или удалить их вовсе, если данные о вас неверны. Однако это не касается случаев, когда сохранение ваших данных необходимо в оправданных коммерческих или юридических целях. Иногда при получении вашей просьбы о предоставлении, редактировании или удалении ваших личных данных мы можем попросить вас подтвердить вашу личность. Мы готовы предоставлять, редактировать и удалять ваши личные данные при условии, что такая просьба от вас не сопряжена для нас с чрезмерными техническими сложностями, не подвергает риску конфиденциальность других пользователей и не поступает слишком многократно.</p>
            <p>Вы также можете попросить нас не использовать ваши личные данные в целях прямого маркетинга. В таком случае ваши данные не будут использованы для этой цели.</p>
            <h3>8. Хранение личных данных</h3><p>Ваши личные данные не будут храниться у нас дольше, чем необходимо для реализации целей, для которых они собирались (п. 5 настоящей Политики конфиденциальности). Вы также можете попросить нас удалить информацию о вас как о пользователе, за исключением тех случаев, когда хранение ваших личных данных требуется от нас по закону или для предоставления вам тех услуг, которые вы заказали. В связи с использованием так называемых облачных технологий хранения данных, использующих серверные ёмкости в разных странах для обеспечения максимальной эффективности и безопасности хранения, ваши личные данные могут располагаться за пределами Республики Беларусь.</p>
            <p>Когда цели, для которых ваши личные данные были собраны, будут достигнуты либо хранение ваших личных данных не будет требоваться от нас по закону, мы предпримем меры по удалению, агрегации или обезличиванию ваших личных данных.</p>
            <p>Информация, которую вы оставили о себе на Squadix, разместив у нас объявление, в любом случае будет сохранена на Squadix после удаления вами объявления в течение 5 лет с целью пресечения попыток злоупотребления нашими сервисами, предупреждения преступной деятельности и содействия в разрешении споров о предметах сделок, объявления о которых были размещены на Squadix.</p>
            <h3>9. Безопасность личных данных</h3><p>Мы храним и обрабатываем ваши личные данные надежным и безопасным образом. Мы следуем нашим процедурам, принципам и мерам, которые гарантируют конфиденциальность пользователей с учетом требований законодательства. Мы постоянно совершенствуем способы сбора, хранения и обработки ваших личных данных. Мы ограничиваем нашим сотрудникам, подрядчикам и партнерам доступ к личным данным, а также накладываем на них строгие договорные обязательства, за нарушение которых предусмотрена ответственность.</p>
            <h3>10. Использование аналитических инструментов и файлов cookie</h3><p>Мы постоянно работаем над совершенствованием качества обслуживания пользователей Squadix. Для достижения наилучших результатов по внедрению новых опций и сервисов мы собираем различную информацию о наших пользователях. Например, это касается информации о тех страницах, которые вы посетили, времени посещения Squadix, а также информации о вашем браузере. Мы также используем различные технические опции, чтобы идентифицировать вас как пользователя и проанализировать полученную информацию о наших пользователях. Методы (в том числе файлы cookie), которые мы применяем в этих целях, необходимы для того, чтобы наши сервисы работали должным образом, чтобы вам было проще пользоваться нашими сервисами, а также чтобы мы смогли проводить исследования, которые бы в будущем способствовали усовершенствованию наших сервисов в соответствии с потребностями пользователей Squadix.</p>
            <h3>11. Изменение политики конфиденциальности</h3><p>Мы будем регулярно обновлять нашу Политику конфиденциальности, чтобы информировать вас обо всех изменениях в нашем подходе к обработке ваших личных данных. В случае принятия существенных изменений, мы будем информировать вас о них отдельно. Все остальные изменения Политики конфиденциальности отражаются на этой странице.</p>
            <p>Последнее обновление политики конфиденциальности: 30 января 2021 года.</p>
            <p>В случае возникновения вопросов о нашей политике конфиденциальности, пожалуйста, свяжитесь с нашей службой поддержки.</p>
        </>
    </StaticContainer>
)

export default Policy;
